import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { MenuItem } from 'react-foundation';

import { visitLinks, educationLinks, exploreLinks, aboutLinks, joinLinks } from '../../constants/navLinks.js/navLinks';

class Dropdown extends Component{
    state = {
        dropdownLinks:[],
        shouldDropdownDisplay: Boolean
    };

    componentDidMount(){
        if(this.props.type === "Visit"){
            this.setState({dropdownLinks:visitLinks});
        } else if(this.props.type==="Education"){
            this.setState({dropdownLinks:educationLinks});
        } else if(this.props.type==="Explore"){
            this.setState({dropdownLinks:exploreLinks});
        } else if(this.props.type==="About Us"){
            this.setState({dropdownLinks:aboutLinks});
        } else {
            this.setState({dropdownLinks:joinLinks});
        }
        this.setState({shouldDropdownDisplay:false});
    }

    displayLinks=()=>{
        const links = [];
        this.state.dropdownLinks.forEach((link,i)=>{
            links.push(
                <Link to={link.href}>{link.title}</Link>
            );
        });
        return links;
    };

    handleMouseOver =()=>{
        this.setState({shouldDropdownDisplay:true});
    };
    handleMouseLeave =()=>{
        this.setState({shouldDropdownDisplay:false});
    };

    render(){
        return(
            <div className="dropdown" onMouseOver={this.handleMouseOver} onMouseLeave={this.handleMouseLeave}>
            <Link to="/visit"><MenuItem>{this.props.type}</MenuItem></Link>
            <div className="dropdown-links" onMouseOver={this.handleMouseOver} onMouseLeave={this.handleMouseLeave} style={this.state.shouldDropdownDisplay ? {dispay:'block'} : {display:'none'}}>
              {this.displayLinks()}
            </div>
          </div>
        );
    };
};

export default Dropdown;
